:root {
  --bannerHeight: 100px;
  --navbarHeight: 40px;
  --headerHeight: calc(var(--bannerHeight) + var(--navbarHeight))
}

/* ########## */
/* Header CSS */
/* ########## */

.banner-container {
  display: flex;
  justify-content: center;
  width: 100%;
  height: var(--bannerHeight);
  background-color: var(--navy1);
  z-index: 998;
}

.banner-content {
  display: flex;
  width: 100%;
  max-width: 1500px;
  padding: 0 2rem 0;
}

.banner-logo {
  margin: 0.7rem 1rem 0.7rem 0rem;
}

/* ########## */
/* Navbar CSS */
/* ########## */

.navbar-container {
  position: sticky;
  z-index: 10;
  top: 0px;
  display: flex;
  justify-content: center;
  width: 100%;
  height: var(--navbarHeight);
  background-color: var(--navy2);
  z-index: 999;
  
  box-shadow: 0 5px 8px rgb(0 0 0 / 0.5);
}

.navbar-btns {
  position: relative;
  z-index: 10;
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  max-width: 1500px;
}

.navbar-btns-group {
  display: flex;
  height: 100%;
}

.navbar-btns-group > * {
  z-index: 10;
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  height: 100%;
  width: 8rem;
  align-items: center;
  cursor: pointer;
  color: var(--text1);
  list-style-type: none;
  text-decoration: none;
  font-weight: bold;

  background-color: var(--navy2);
  transition: ease-in-out 200ms;
}

.navbar-btns-group > * bar {
  display: flex;
  margin-top: 0.4rem;
  height: 0.2rem;
  width: 100%;
  background-color: var(--orange2);
  transform: scaleX(0);
  transform-origin: left;
  transition: ease-in-out 200ms;
}

.home-navbar:hover .home-nav-highlight {
  transform: scaleX(1);
}

.products-navbar:hover .products-nav-highlight {
  transform: scaleX(1);
}

.control-navbar:hover .control-nav-highlight {
  transform: scaleX(1);
}

.services-navbar:hover .services-nav-highlight {
  transform: scaleX(1);
}

.about-navbar:hover .about-nav-highlight {
  transform: scaleX(1);
}

.contact-navbar:hover .contact-nav-highlight {
  transform: scaleX(1);
}

.adsysapp-login {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100%;
  width: 6rem;
  text-decoration: none;
  cursor: pointer;
  padding: 0 1rem 0 1rem;
  background-color: rgb(56, 79, 129);
  color: black;
}

/* ############ */
/* Dropdown CSS */
/* ############ */

.products-navbar:hover .products-ddm {
  visibility: visible;
  transform: scaleY(100%);
  opacity: 1;
}

.control-navbar:hover .control-ddm {
  visibility: visible;
  transform: scaleY(100%);
  opacity: 1;
}

.services-navbar:hover .services-ddm {
  visibility: visible;
  transform: scaleY(100%);
  opacity: 1;
}

.dropdown-menu {
  position: absolute;
  z-index: -5;
  visibility: hidden;
  width: 100%;
  max-width: 1500px;
  height: auto;
  margin-top: 0;
  min-width: max-content;
  top: var(--navbarHeight);
  background-color: var(--navy1);
  left: 0;

  opacity: 0;
  transform: scaleY(0);
  transform-origin: top;
  transition: all 200ms;
}

.dropdown-main-row {
  display: grid;
  grid-auto-flow: column;
  grid-auto-columns: 1fr;
  width: 100%;
}

.dropdown-column {
  background-color: var(--navy2);
  padding: 1rem;
  margin: 1rem;
}

.dropdown-subheading {
  background-color: var(--navy2);
  padding: 1rem;
  list-style-type: none;
}

/*  */

.dropdown-second-row {
  display: grid;
  grid-auto-flow: row;
  /* grid-auto-columns: 1fr; */
  width: 100%;
}

.dropdown-conveyer-row {
  background-color: var(--navy2);
  padding: 0.5rem;
  margin: 0.5rem;
}

.dropdown-conveyer-subheading-container {
  display: flex;
}

.dropdown-conveyer-subheading {
  background-color: var(--navy2);
  padding: 0.5rem;
  list-style-type: none;
}